.container {
  background-image: url(./assets/mobile/bg-image-nighttime.jpg);
  width: 100vw;
  height: 100vh;
  background-size: cover;
  mix-blend-mode: normal;
  overflow-x: hidden;

}
@media screen and (min-width: 600px) {
  .container {
    background: url(./assets/desktop/bg-image-nighttime.jpg) no-repeat center center fixed  ;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    /* height: 100vh; */
    mix-blend-mode: normal;
    overflow-x: hidden;
  }
}
.containerDay {
  background: url(./assets/mobile/bg-image-daytime.jpg);
  width: 100vw;
  height: 100vh;
  background-size: cover;
  mix-blend-mode: darken;
  overflow-x: hidden;
}
@media screen and (min-width: 600px) {
  .containerDay {
    background-image: url(./assets/desktop/bg-image-daytime.jpg);
    background-size: cover;
    width: 100vw;
    height: 100vh;
    mix-blend-mode: normal;
    overflow-x: hidden;
  }
}
